import React, { useState, useEffect, useRef } from "react";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import MainContainer from "../components/MainContainer";
import Header from "../components/Header";
import mbti_data from "../data/results.json";
import styled from "styled-components";
import Mark from "../assets/image/Bookmark.png";
import Combi from "../data/Combination.json";
import Toast from "./Toast";
import { changeToText, getRandomSpot, getURLs, sortedAnsList } from "../utils/utils";
import Button from "../components/Button";
import { Background } from "../components/LoadingStyle";
import Spinner from "../assets/image/Spinner1.gif";
import Typography from "../components/Typography";
import axios from "axios";
import { CopyToClipboard } from "react-copy-to-clipboard";
import bookMarkIcon from "../assets/image/Bookmark.png";
import bookMarkActiveIcon from "../assets/image/bookMarkActive.png";

const Result = () => {
  // const location = useLocation();
  const { state } = useLocation();
  // const location = useLocation();
  const [searchParams, setSearchParams] = useSearchParams();
  // const number = location.number;
  const navigate = useNavigate();
  // const { answersArr, answerTextCombined } = location.state;
  const [loading, setLoading] = useState(false);
  const [mbti, setMbti] = useState("");
  const [imagePath, setImagePath] = useState("");
  const [nickName, setNickName] = useState("");
  const [comment, setComment] = useState("");
  const [goodCombi, setGoodCombi] = useState("");
  const [goodImagePath, setGoodImagePath] = useState("");
  const [goodCombiName, setGoodCombiName] = useState("");
  const [badImagePath, setBadImagePath] = useState("");
  const [badCombi, setBadCombi] = useState("");
  const [badCombiName, setBadCombiName] = useState("");
  const [placeList, setPlaceList] = useState([]);
  const [listCount, setListCount] = useState(3);
  const [refreshLoading, setRefreshLoading] = useState(false);
  const number = searchParams.get("number");
  const [isApp, setIsApp] = useState(null);
  const [userId, setUserId] = useState("");
  const [jwt, setJwt] = useState("");
  const [rnSend, setRnSend] = useState(false);
  const [platform, setPlatform] = useState(null);
  const [bookMarkList, setBookMarkList] = useState([]);
  const [showToast, setShowToast] = useState(false);
  const [rnData, setRnData] = useState({});
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [reLoad, setReLoad] = useState(true);
  const [saveState, setSaveState] = useState();

  useEffect(() => {
    window.ReactNativeWebView?.postMessage(JSON.stringify(true)); // 웹뷰와 통신
    //android
    document.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        setRnData(parseData);
        setReLoad(parseData.reLoad);
      } catch (err) {
        console.log(err);
      }
    });
    //ios
    window.addEventListener("message", (e) => {
      try {
        const parseData = JSON.parse(e.data);
        // alert(JSON.stringify(parseData));
        setRnData(parseData);
        setReLoad(parseData.reLoad);
      } catch (err) {
        console.log(err);
      }
    });
    // 카카오톡 sdk 추가
    const script = document.createElement("script");
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    script.async = true;
    document.body.appendChild(script);
    return () => document.body.removeChild(script);
  }, []);

  const closeModal = () => {
    setShowLoginModal(false);
  };

  const getData = async () => {
    setLoading(true);
    await axios({
      method: "get",
      url: `${process.env.REACT_APP_API_URL}/v2/result?id=${state?.userId}`,
      headers: {
        Authorization: state?.jwt,
      },
    })
      .then((res) => {
        // console.log(res.data);
        setMbti(res.data.mbti.mbti);
        setImagePath(`https://dltour.data-labs.co.kr/api/upload/${res.data.mbti.mbti}.png`);
        setNickName(res.data.mbti.nickName);
        setComment(res.data.mbti.comment);
        setGoodCombi(res.data.mbti.goodCombi);
        setGoodImagePath(
          `https://dltour.data-labs.co.kr/api/upload/${res.data.mbti.goodCombi}.png`
        );
        setGoodCombiName(res.data.goodCombiName);
        setBadCombi(res.data.mbti.badCombi);
        setBadImagePath(
          `https://dltour.data-labs.co.kr/api/upload/${res.data.mbti.badCombi}.png`
        );
        setBadCombiName(res.data.badCombiName);
        setPlaceList(res.data.recommandPlace);
        const copyArr = [...res.data.recommandPlace];
        const bookMarkArr = copyArr.map((el) => el.bookMark);
        setBookMarkList(bookMarkArr);
        console.log(bookMarkList);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const linkCopyData = async () => {
    setLoading(true);
    await axios({
      method: "get",
      url: `${process.env.REACT_APP_API_URL}/v2/result/display?id=${number}`,
    })
      .then((res) => {
        setMbti(res.data.mbti.mbti);
        setImagePath(`https://dltour.data-labs.co.kr/api/upload/${res.data.mbti.mbti}.png`);
        setNickName(res.data.mbti.nickName);
        setComment(res.data.mbti.comment);
        setGoodCombi(res.data.mbti.goodCombi);
        setGoodImagePath(
          `https://dltour.data-labs.co.kr/api/upload/${res.data.mbti.goodCombi}.png`
        );
        setGoodCombiName(res.data.goodCombiName);
        setBadCombi(res.data.mbti.badCombi);
        setBadImagePath(
          `https://dltour.data-labs.co.kr/api/upload/${res.data.mbti.badCombi}.png`
        );
        setBadCombiName(res.data.badCombiName);
        setPlaceList(res.data.recommandPlace);
        const copyArr = [...res.data.recommandPlace];
        const bookMarkArr = copyArr.map((el) => el.bookMark);
        setBookMarkList(bookMarkArr);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    if (number) {
      linkCopyData();
    } else {
      getData(); 
    }
    // 카카오톡 sdk 추가
    const script = document.createElement("script");
    script.src = "https://developers.kakao.com/sdk/js/kakao.js";
    script.async = true;
    document.body.appendChild(script);
    return () => document.body.removeChild(script);

  },[]);

  useEffect(() => {
    // console.log('저장하고 와서도 얘가 뜰까?',rnData)
    // alert(JSON.stringify(rnData));
    const copyArr = [...bookMarkList];
    const index = rnData.contentId;
    if (index >= 0 && index < copyArr.length) {
      copyArr[index] = rnData.boolean; 
      }
    setBookMarkList(copyArr);
    }
, [rnData]);

  const shareToKatalk = async () => {
    const copyArr = [...placeList];
    const result = copyArr.filter((el, idx) => idx !== 0);
    const result2 = result.map((el) => {
      return el.id;
    });
    
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/v2/result/share`,
      headers: {
        Authorization: state?.jwt,
      },
      data: {
        userId: state?.userId,
        placeIDarray: result2,
      },
    })
      .then((res) => {
        console.log(res.data);
        if (state.isApp) {
          const data = {
            name: "resultApp",
            id: res.data.id,
            mbtiImg: `https://dltour.data-labs.co.kr/api/upload/${mbti}.png`,
          };
          window.ReactNativeWebView?.postMessage(JSON.stringify(data));
        } else {
          if (res.data.isPass) {
            if (window.Kakao) {
              const kakao = window.Kakao;

              if (!kakao.isInitialized()) {
                // javascript key
                kakao.init("69c873f1cf4e580ab99c761390d9777e");
              }
              kakao.Link.sendDefault({
                objectType: "feed",
                content: {
                  title: "MBTI 알아보러 가실래요?",
                  description: "개인 MBTI에 맞는 여행지를 추천해드려요 ~!",
                  imageUrl: `https://dltour.data-labs.co.kr/api/upload/${mbti}.png`,
                  imageWidth: 400,
                  imageHeight: 400,
                  link: {
                    mobileWebUrl: `${process.env.REACT_APP_WEB_URL}/result?number=${res.data.id}`,
                    webUrl: `${process.env.REACT_APP_WEB_URL}/result?number=${res.data.id}`,
                  },
                },
                buttons: [
                  {
                    title: "결과 확인하기",
                    link: {
                      // 기본 도메인 설정값으로 가짐 (링크를 뭘 주던) 수정
                      mobileWebUrl: `${process.env.REACT_APP_WEB_URL}/result?number=${res.data.id}`,
                      webUrl: `${process.env.REACT_APP_WEB_URL}/result?number=${res.data.id}`,
                      
                    },
                  },
                ],
              });
            }
          }
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const reTry = () => {
    navigate("/question", {
      state: {
        userId: state?.userId,
        jwt: state?.jwt,
        isApp: state?.isApp,
        platform: state?.platform,
      },
    });
  };

  const showMore = () => {
    setListCount(placeList?.length);
  };
  const placeRefresh = async () => {
    setRefreshLoading(true);
    await axios({
      method: "post",
      url: `${process.env.REACT_APP_API_URL}/v2/result/reload`,
      headers: {
        Authorization: state?.jwt,
      },
      data: {
        id: state?.userId,
      },
    })
      .then((res) => {
        // console.log(res.data);
        setPlaceList(res.data);
        const copyArr = [...res.data];
        const bookMarkArr = copyArr.map((el) => el.bookMark);
        setBookMarkList(bookMarkArr);
        setRefreshLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleDetailSaveClick = (el, idx) => {
    //저장함으로 이동
      if (state.isApp) {
        const sendData = {
          name: "modalOpen",
          contentId: el.id,
          selectedIdx: idx
        };
        window.ReactNativeWebView?.postMessage(JSON.stringify(sendData));
    } else {
        alert("로그인이 필요합니다. 여행대로 App을 이용해주세요.");
    }
  };
  
  const moveDetail = (contentId, title) => {
    if (state.isApp) {
      const data = {
        name: "moveDetail",
        contentId: contentId,
        title: title,
      };
      window.ReactNativeWebView?.postMessage(JSON.stringify(data));
    } else {
      // navigate(`/placedetail/${contentId}/${typeId}`);
      window.open(`${process.env.REACT_APP_WEB_URL}/${contentId}`);
    }
  };


  const saveEvent = () => {
    const isIos = /iPhone|iPad|iPod/i.test(navigator.userAgent);
    const isAndroid = /android/i.test(navigator.userAgent);
    if (state.isApp) {
      // 앱일때
      if (state.jwt) {
        alert("저장되었습니다.");
      } else {
        alert("로그인 후 이용해주세요.");
      }
    } else {
      // 앱이아닐떄
      if (isIos) {
        if (window.confirm("여행대로 App을 설치하시겠습니까?")) {
          window.open(
            "https://apps.apple.com/kr/app/%EC%97%AC%ED%96%89%EB%8C%80%EB%A1%9C-%EC%97%AC%ED%96%89%EC%B6%94%EC%B2%9C-%EB%B0%8F-%EC%9D%BC%EC%A0%95-%EA%B3%B5%EC%9C%A0%ED%95%98%EA%B8%B0/id6458978344"
          );
        } else {
          return;
        }
      } else if (isAndroid) {
        if (window.confirm("여행대로 App을 설치하시겠습니까?")) {
          window.open("https://play.google.com/store/apps/details?id=com.datatourapp");
        } else {
          return;
        }
      } else {
        alert("여행대로 App을 이용해주세요.");
      }
    }
  };

  return (
    <MainContainer>
      <Header />
      {loading ? (
        <div
          style={{
            marginTop: 150,
          }}
        >
          <Background>
            <img src={Spinner} alt="Loading" />
          </Background>
        </div>
      ) : (
        <>
          <WhatisTravleMbti>
            <MbtiTypeText>{mbti}</MbtiTypeText>
          </WhatisTravleMbti>
          <MbtiAnimalImage src={imagePath} alt="MBTI animal Image" />
          {/* <WhatisMbti>{mbti}</WhatisMbti> */}
          <MbtiAnimal>{nickName}</MbtiAnimal>
          <MbtiDescription>{comment}</MbtiDescription>
          <LetsGo>
            {mbti}에 맞는 여행지를 저장 <br /> 여행을 떠나봐요~
          </LetsGo>
          {refreshLoading ? (
            <Background>
              <img src={Spinner} alt="Loading" />
            </Background>
          ) : (
            <RecommendationsContainer>
              {placeList?.map((el, idx) => {
                if (idx < listCount) {
                  return (
                    <RecommendationItem
                    key={idx}
                    >
                      <RecommendationTheme>{el?.describe}</RecommendationTheme>
                      <ImageArea>
                        <RecommendationImage 
                          src={el?.first_image} 
                          onClick={() => moveDetail(el.id ,el.title)}/>
                          {/* {el.bookMark ? (
                              <BookMark 
                                onClick={() => handleDetailSaveClick(el)}
                                src={require("../assets/image/bookMarkActive.png")} 
                              />
                            ) : (
                              <BookMark 
                                onClick={() => handleDetailSaveClick(el)}
                                src={require("../assets/image/Bookmark.png")} />
                            )} */}
                                  

                        <BookMark
                          src={bookMarkList[idx] ? bookMarkActiveIcon : bookMarkIcon}
                          onClick={() => handleDetailSaveClick(el, idx)}
                        />
                      </ImageArea>
                      <RecommendationText>{el?.title}</RecommendationText>
                    </RecommendationItem>
                  );
                }
              })}
            </RecommendationsContainer>
          )}
          {listCount !== 9 ? (
            // <BtnContainer>
            //   <BlueButton1 onClick={showMore}>추천여행지 더보기</BlueButton1>
            // </BtnContainer>
            <Button
              type={"basic"}
              height={"58px"}
              onClick={() => showMore()}
              style={{ margin: "0.5em 0 2.5rem 0" }}
            >
              <Typography type="light">추천 여행지</Typography>{" "}
              <Typography type="bold">더보기</Typography>
            </Button>
          ) : (
            // <BtnContainer>
            //   <BlueButton1 onClick={()=>moreRandom(answersArr)}>추천여행지 새로고침</BlueButton1>
            // </BtnContainer>
            <Button
              type={"basic"}
              height={"58px"}
              onClick={() => placeRefresh()}
              style={{ margin: "0.5em 0 2.5rem 0", display: refreshLoading ? "none" : "initial" }}
            >
              <Typography type="light">추천 여행지</Typography>{" "}
              <Typography type="bold">새로고침</Typography>
            </Button>
          )}
          <CompatibilityContainer>
            <CompatibilityColumn>
              <CompatibilityItem>
                <CompatibilityText>잘 맞는 궁합</CompatibilityText>
                <CompatibilityGoodText>{goodCombi}</CompatibilityGoodText>
                <CompatibilityImage src={goodImagePath} alt="GoodCompt" />
                <CompatibilityDesc>{goodCombiName}</CompatibilityDesc>
              </CompatibilityItem>
            </CompatibilityColumn>
            <CompatibilityColumn>
              <CompatibilityItem>
                <CompatibilityText>안 맞는 궁합</CompatibilityText>
                <CompatibilityBadText>{badCombi}</CompatibilityBadText>
                <CompatibilityImage src={badImagePath} alt="BadCompt" />
                <CompatibilityDesc>{badCombiName}</CompatibilityDesc>
              </CompatibilityItem>
            </CompatibilityColumn>
          </CompatibilityContainer>

          {number ? (
            <BtnArea>
              <BigReTryBtn onClick={reTry}>나도 해볼래!</BigReTryBtn>
            </BtnArea>
          ) : (
            <>
              <BtnArea>
                <LinkCopyBtn onClick={() => shareToKatalk()}>공유하기</LinkCopyBtn>
                <ReTryBtn onClick={reTry}>흠...다시 해볼래</ReTryBtn>
              </BtnArea>
              <SaveBtn onClick={() => saveEvent()}>저장하기</SaveBtn>
            </>
          )}

          {/* 
      <BtnContainer><BlueButton2 onClick={handleButtonClick}>내 지인은 뭘까? 공유하러가기!</BlueButton2></BtnContainer>
      <BtnContainer><WhiteButton onClick={ReTry}>흠... 다시 해볼래</WhiteButton></BtnContainer>
          {/* <Button
        type={"basic"}
        height={"58px"}
        onClick={handleButtonClick}
        style={{ margin: "1em 0 0 0" }}
      >
        <Typography type="bold">링크복사</Typography>
        <Typography type="light">로 친구에게 결과 알려주기</Typography>
      </Button>
      <Button height={"58px"} onClick={ReTry} style={{ margin: "1em 0 5.875rem 0" }}>
        흠... 다시 해볼래
      </Button> */}
          {/* {showToast && (
        <Toast message="링크가 복사되었습니다!!" duration={1000} onClose={handleToastClose} />
      )} */}
        </>
      )}
      {showLoginModal && (
        <ShareModalBackground onClick={closeModal}>
          <AlertModalContent>
            <AlertModalContainer>
              <Message>로그인이 필요합니다.</Message>
              <ModalButton onClick={closeModal}>닫기</ModalButton>
            </AlertModalContainer>
          </AlertModalContent>
        </ShareModalBackground>
      )}
    </MainContainer>
  );
};

